'use client'

import React, { useRef, useState } from 'react'
import { Button } from '@/components/ui/button'
import Link from 'next/link'
import Image from 'next/image'



const StudentForm = () => {
  const [image, setImage] = useState<string | null>(null)
  const fileInputRef = useRef<HTMLInputElement>(null)

  const handleImageClick = () => {
    fileInputRef.current?.click()
  }

  const handleImageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const file = e.target.files?.[0]
    if (file) {
      const reader = new FileReader()
      reader.onload = (ev) => setImage(ev.target?.result as string)
      reader.readAsDataURL(file)
    }
  }

  return (
    <>
      <Button
        asChild
        className="mb-10 w-fit border bg-white text-xs text-dark-200 hover:bg-light-300"
      >
        <Link href="/school/student" className="flex items-center gap-2 text-2xl font-semibold">
          <Image
            src="/icons/undo-arrow.svg"
            alt="Back"
            width={20}
            height={20}
          />
          Go back
        </Link>
      </Button>
      <section className="flex flex-col">
        {/* Uploadable Circle */}
        <div
          className="w-36 h-36 rounded-full bg-orange-200 flex items-center justify-center cursor-pointer mb-6 overflow-hidden"
          onClick={handleImageClick}
        >
          {image ? (
            // eslint-disable-next-line @next/next/no-img-element
            <img src={image} alt="Profile" className="w-full h-full object-cover" />
          ) : (
            <span className="text-6xl text-white">+</span>
          )}
          <input
            type="file"
            accept="image/*"
            ref={fileInputRef}
            className="hidden"
            onChange={handleImageChange}
          />
        </div>
        {/* Form */}
        <form className="w-full max-w-2xl space-y-4">
          <div>
            <label className="block mb-1">ឈ្មោះ:</label>
            <input className="w-full rounded border px-4 py-2" placeholder="Enter name" />
          </div>
          <div className="flex gap-4">
            <div className="flex-1">
              <label className="block mb-1">ថ្ងៃខែឆ្នាំកំណើត</label>
              <input type="date" className="w-full rounded border px-4 py-2" placeholder="Select date" />
            </div>
            <div className="flex-1">
              <label className="block mb-1">ភេទ</label>
              <select className="w-full rounded border px-4 py-2">
                <option value="">Select</option>
                <option value="male">ប្រុស</option>
                <option value="female">ស្រី</option>
              </select>
            </div>
          </div>
          <div>
            <label className="block mb-1">អាសយដ្ឋាន</label>
            <input className="w-full rounded border px-4 py-2" placeholder="Enter address" />
          </div>
          <div>
            <label className="block mb-1">លេខទូរស័ព្ទមាតាបិតា</label>
            <input className="w-full rounded border px-4 py-2" placeholder="Enter phone number" />
          </div>
          <Button type="submit" className="w-full bg-[#25388C] hover:bg-[#1e2e6d] text-white text-lg">
            បញ្ចូល
          </Button>
        </form>
      </section>
    </>
  )
}

export default StudentForm